import React from "react";
import styles from "./ticket.module.scss";

const Index = ({
  price,
  arrival,
  departure,
  transfer,
  airlines,
  duration,
  stop,
  id,
}) => {
  return (
    <div className={styles.wrapper}>
      <div className={styles.main}>
        <div className={styles.price}>: {price}</div>
        <div className={styles.company}> {airlines}</div>
      </div>
      <div className={styles.detail}>
        <p>
          {departure.iataCode} - {arrival.iataCode}
        </p>
        <p> </p>
        <p>{stop === 0 ? " " : `${stop} `}</p>
        <p>
          {departure.at.substring(11, 16)} - {arrival.at.substring(11, 16)}
        </p>
        <p>
          {duration.substring(2).replace("H", "  ").replace("M", " ")}
        </p>
        <p>{transfer ? transfer[0].iataCode : " "}</p>
      </div>
    </div>
  );
};

export default Index;
